/* Gradients / Overlays
-------------------------------------------------------*/
.bg-gradient-bottom:before {
	content: "";
	top: 0;
	display: block;
	position: absolute;
	width: 100%;
	height: 100%;
	background: #131313;
	/* Old browsers */
	background: -moz-linear-gradient(bottom, #131313 0%, transparent 100%);
	/* FF3.6+ */
	background: -webkit-gradient(linear, left top, left bottom, color-stop(0%, #131313), color-stop(100%, transparent));
	/* Chrome,Safari4+ */
	background: -webkit-linear-gradient(bottom, #131313 0%, transparent 100%);
	/* Chrome10+,Safari5.1+ */
	background: -o-linear-gradient(bottom, #131313 0%, transparent 100%);
	/* Opera 11.10+ */
	background: -ms-linear-gradient(bottom, #131313 0%, transparent 100%);
	/* IE10+ */
	background: linear-gradient(to top, #131313 0%, transparent 100%);
	/* W3C */
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#000000',GradientType=0 );
	/* IE6-9 */ }

.bg-overlay {
	position: relative; }
	.bg-overlay:before {
		content: "";
		top: 0;
