/* Animations
-------------------------------------------------------*/
.animate {
	position: relative;
	overflow: hidden; }
	.animate-container {
		opacity: 0;
		visibility: hidden;
		-webkit-transition: 0.2s ease-in-out 0.8s;
		-moz-transition: 0.2s ease-in-out 0.8s;
		-ms-transition: 0.2s ease-in-out 0.8s;
		-o-transition: 0.2s ease-in-out 0.8s;
		transition: 0.2s ease-in-out 0.8s; }
	.animate:before {
		content: "";
		position: absolute;
		display: block;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0; }
	.animate:before {
		background-color: #F6F6F6;
		z-index: 1;
		-webkit-transform: translateX(-101%);
		-moz-transform: translateX(-101%);
		-ms-transform: translateX(-101%);
		-o-transform: translateX(-101%);
		transform: translateX(-101%); }
	.animate--animated:before {
		animation: deothemes-appearing-animation 1s cubic-bezier(0.86, 0, 0.07, 1) forwards; }
	.animate--animated .animate-container {
		opacity: 1;
		visibility: visible; }

@-webkit-keyframes deothemes-appearing-animation {
	0% {
		-webkit-transform: translateX(-101%);
		transform: translateX(-101%); }
	50% {
		-webkit-transform: translateX(0);
		transform: translateX(0); }
	100% {
		-webkit-transform: translateX(101%);
		transform: translateX(101%); } }

@keyframes deothemes-appearing-animation {
	0% {
		-webkit-transform: translateX(-101%);
		transform: translateX(-101%); }
	50% {
		-webkit-transform: translateX(0);
		transform: translateX(0); }
	100% {
		-webkit-transform: translateX(101%);
		transform: translateX(101%); } }
